.property_alert_backdrop{
  position: fixed;
  background-color: #00000078;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  overflow-y: scroll;
  display: none;
  z-index: 999;
  font-family: Roboto-Regular,sans-serif;


  div.property_alert_container{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: calc(100vw - 30px);
    max-width: 715px;

    @media (max-width: 767.98px) {
      //flex-direction: column;
    }

    .close-btn{
      top: -8px;
      font-size: 16px;
      position: absolute;
      right: -6px;
      cursor: pointer;
      z-index: 9;
      i{
        background-color: #fff;
        border-radius: 50%;
        margin: 0;
        width: 13px;
        height: 13px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #000;
      }
    }

    .property_alert-header{
      background-color: #009431;
      color: #FFFFFF;
      text-align: center;
      padding: 13px 15px;
      font-size: 18px;
      text-transform: uppercase;
      font-weight: 400;
      letter-spacing: .5px;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
      position: relative;

      &:after{
        position: absolute;
        top: 31%;
        left: 50%;
        transform: translateX(-50%);
        content: "\f0d7";
        font-family: FontAwesome;
        font-size: 6rem;
        color: #009431;
      }
    }

    .property_alert_body{
      background-color: #FFFFFF;
      padding: 40px 25px 25px;
      text-align: left;
      color: #444444;

      form{
        .form-control,.dropdown-toggle{
          font-size: 13px !important;
          font-weight: normal !important;
          font-family: Roboto-Regular,sans-serif;
        }
        .form-group{
          margin-bottom: 0;
          .form-control,.radio-inline{
            margin-bottom: 15px;
          }
        }
        margin-top: 20px;
        .dropdown-check{
          button.form-control{
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
          }

          .dropdown-menu{
            padding: 5px 10px 15px 10px;
           ul{
             list-style-type: none;
           }
            li{
              &,&:hover{
                text-align: left;
                background-color: white;
              }
            }

            .nested-drodown-ul{
              padding-left: 25px;
            }
          }
        }
        #max-budget{
          li{
            padding-top: 7px;
            label{
              font-weight: normal;
            }

            .checkbox input[type="checkbox"] {
              opacity: 0;
            }
          }
        }

        .btn{
          min-height: 40px !important;
          margin-bottom: 15px;
          border-radius: 4px !important;
          &:focus{
            outline: none !important;
          }
        }

        .btn-default{
          &,&:hover{
            border: 1px solid #cccccc !important;
          }
        }

        .btn-success{
          background-color: #009431 !important;
        }

        .error{
          color: red;
          font-weight: 400;
        }
      }
    }
  }
}


.newsLetter_backdrop {
  position: fixed;
  background-color: #00000078;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  overflow-y: scroll;
  z-index: 999;
  font-family: Roboto-Regular,sans-serif;


  div.newsLetter_container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100vw - 30px);
    max-width: 715px;
    padding: 10px;

    @media (max-width: 767.98px) {
      //flex-direction: column;
    }

    >div{
      padding: 25px 15px 15px;
      position: relative;
      background-color: #FFFFFF;

      .close-btn{
        top: -8px;
        font-size: 16px;
        position: absolute;
        right: -6px;
        cursor: pointer;
        i{
          background-color: #fff;
          border-radius: 50%;
          margin: 0;
          width: 13px;
          height: 13px;
          display: flex;
          justify-content: center;
          align-items: center;
          color: #000;
        }
      }

      img{
        margin-bottom: 15px;
      }

      h3{
        text-transform: uppercase;
        line-height: 1.3;
        color: #444;
      }
      p{
        font-size: 15px;
        font-style: italic;
        color: #6f6f6f;
        margin-top: 15px;
        margin-bottom: 25px;
      }

      .row{
        justify-content: center;
        display: flex;

        >div{
          @media (min-width: 576px) and (max-width: 767.98px) {
            max-width: 75% !important;
          }
        }
      }

      form{
        margin-bottom: 20px;
        .input-group-lg>.form-control, .input-group-lg>.input-group-addon, .input-group-lg>.input-group-btn>.btn {
          height: 43px;
          padding: 10px 16px;
          font-size: 15px;
        }
        .input-group-lg>.form-control{
          border-top-left-radius: 4px;
          border-bottom-left-radius: 4px;
        }
        .input-group-lg>.input-group-btn>.btn {
          border-color: #009431 !important;
          border-top-right-radius: 4px;
          border-bottom-right-radius: 4px;
        }
      }

      .btn-success{
        background-color: #009431!important;
      }
    }


  }
}